import { StyledMovieList } from "./styles";
import { IMovieDetails } from "types/types";
import { MovieListItemFavorites } from "components";
import { useWindowSize } from "hooks";
import { Breackpoint } from "ui";

interface IProps {
  movies: IMovieDetails[];
}

export const MoviesListFavorites = ({ movies }: IProps) => {
  const { screenWidth } = useWindowSize();

  const getCardount = (): number => {
    if (screenWidth) {
      if (screenWidth > 1900) {
        return 6;
      } else if (screenWidth > 1700) {
        return 5;
      } else if (screenWidth > 1200) {
        return 4;
      } else if (screenWidth > 800) {
        return 3;
      } else if (screenWidth > Breackpoint.SM) {
        return 2;
      }
    }
    return 1;
  };
  return (
    <StyledMovieList $Cardount={getCardount()}>
      {movies.map((movie) => {
        return <MovieListItemFavorites movie={movie} key={movie.id} />;
      })}
    </StyledMovieList>
  );
};
